import React  from 'react';



const Vefrit = () =>

  <div>
      <h1>Plógur - Vefrit</h1>
      <div>
            <p>
                  Landbúnaðarsafn Íslands stofnar til vefritsins Plógur. Vefritinu er ætlað að vera vettvangur til birtingar greina um íslenska búnaðarsögu eða viðfangsefni tengt henni. Vefritið á að varðveita og miðla lýsandi, greinandi, fræðandi og skemmtandi fróðleik um einkenni, þróun og stöðu íslensks landbúnaðar á ýmsum tímum og á ýmsum svæðum.
                  Vefritið kemur út eftir efnum og ástæðum. Útkoma nýs efnis ritsins verður kynnt hverju sinni í fréttadálki heimasíðu safnsins www.landbunadarsafn.is. Efni ritsins verður eingöngu birt þar. Eftir ástæðum og því, sem við á, er þó gert ráð fyrir að efni þess birtist á öðru formi, t.d. prentað.
                  Áhersla er lögð á að höfundar og notendur efnis vefritsins virði viðurkenndar og staðfestar reglur um höfundarrétt og meðferð alls efnis í ritinu. Landbúnaðarsafn áskilur sér rétt til þess að framfylgja þeim með hefð- og lagabundnum hætti.
            </p>
            <p>
                  Vefritinu verður skipt í tvær deildir, A og B:
            </p>
            <p>
                  A-deild:  greinar byggðar á rannsóknum höfunda og mega teljast sjálfstæð viðbót við núverandi þekkingu. Varðandi frágang greina er bent á almennar reglur um ritun og frágang sem kenndar eru á fyrstu árum háskólanáms. Til fullkomnunar má þó benda á reglur tímaritsins Saga sem verðuga fyrirmynd, sjá www.sogufelag.is/timarit
                  /timarit_ritreglur.html  Greinar verða lesnar yfir af kunnáttumönnum (ritrýndar) áður en birtar verða. Með vísun til álits ritrýnenda áskilur ritstjóri sér rétt til þess að hafna greinum ef svo ber undir.                  
            </p>
            <p>
                  B-deild:  efni almenns eðlis, frásagnir og fróðleikur um afmörkuð viðfangsefni án þess þó að teljast lauslegt spjall eða samtíningur. Efnið getur m.a. verið ritaðar greinar, skyggnusyrpur (PPT-skrár) ofl.
            </p>

            <p>
                  Þeir, sem efni eiga í fórum sínum, eru hvattir til þess að senda það til birtingar. Ritstjóri áskilur sér rétt til breytinga á formi og framsetningu efnis í samráði við höfund/höfunda.
            </p>

            <p>
                  Það er megin stefna vefritsins Plógur að bera fram efni sem ætla má að höfði til hins áhugasama og upplýsta almennings og bæti honum fróðleik og skemmtan, auk þess að forða frá glötun og gleymsku efni sem varðar sögu og þróun landbúnaðar á Íslandi.
                  Ábyrgðarmaður og ritstjóri vefritsins verður um sinn Bjarni Guðmundsson á Hvanneyri.
            </p>
      </div>

  </div>





export default Vefrit;



